import * as React from 'react';
import { Grid } from '@material-ui/core/';

import { InformationLabel } from 'Components/UI/InformationLabel';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

interface MemberInformationProps {
  data: any;
}

export const MemberInformation: React.FC<MemberInformationProps> = (
  props: MemberInformationProps,
): JSX.Element => {
  const { data } = props;

  return (
    <Grid
      container
      direction="row"
      alignItems="stretch"
      style={{ paddingTop: '35px' }}
      spacing={5}
    >
      <Grid item xs={3}>
        <InformationLabel
          name={'CORPORATE ACCOUNT'}
          value={
            data.client_object !== undefined &&
            data.client_object !== null &&
            data.client_object.registered_name !== undefined &&
            data.client_object.registered_name !== null
              ? data.client_object.registered_name
              : ''
          }
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'CHANNEL USED'}
          value={data.channel ? data.channel : ''}
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'SENDER'}
          value={data.sender_name ? data.sender_name : ''}
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'EMAIL ADDRESS'}
          value={data.sender_email ? data.sender_email : ''}
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'DATE SENT'}
          value={
            data.date_sent
              ? GlobalFunction.toCommonDateString(new Date(data.date_sent))
              : ''
          }
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'BATCH NAME'}
          value={data.batch_name ? data.batch_name : ''}
        />
      </Grid>
      <Grid item xs={3}>
        <InformationLabel
          name={'NO. OF MEMBERS'}
          value={data.no_of_members ? data.no_of_members : 0}
        />
      </Grid>
    </Grid>
  );
};

MemberInformation.defaultProps = {
  data: {},
};
