import React from 'react';
import {
    Grid,
    Table,
    TableHeaderRow,
    PagingPanel,
    SearchPanel,
    Toolbar,
    TableFilterRow,
    TableSelection
} from '@devexpress/dx-react-grid-material-ui';

import {
    PagingState,
    CustomPaging,
    SortingState,
    SearchState,
    FilteringState,
    SelectionState,
    IntegratedSelection
} from '@devexpress/dx-react-grid';

import MomentUtils from '@date-io/moment';

import * as MaterialUI from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faSort,
    faSortUp,
    faSortDown,
    faSearch
} from '@fortawesome/free-solid-svg-icons';

import { STYLE_CONST, COLUMN_EXTENSIONS } from '../ProcessBatches.const';
import { StatusItems } from 'Components/UI/TableComponent/DropdownConstants';

const datePickerStyles = makeStyles({
    keyboardDatePicker: {
        width: '100%',
    },
});

const Select = ({ items, ...restProps  }: any) => {
    return (
        <MaterialUI.Select
            variant='outlined'
            style={{ width: '100%' }}
            renderValue={(selected: any) => {
                if (selected != null) {
                    const value: any = items.find((item: any) => {
                        return item.value === selected;
                    });
                    if (value) {
                        return value.label;
                    } else {
                        return '——';
                    }
                }
                return '——';
            }}
            {...restProps}
        >
            {items.map((item: any, i: number) => (
                <MaterialUI.MenuItem key={i} value={item.value}>
                    {item.label}
                </MaterialUI.MenuItem>
            ))}
        </MaterialUI.Select>
    );
};

const ClientsFilterCell = (props: any) => {
    const { column, filter, onFilter, filteringEnabled, classes } = props;
    const datePickerClasses = datePickerStyles();
    if (filteringEnabled) {
        if (column.name === 'member_status') {
            const items = StatusItems;

            return  (
                <MaterialUI.TableCell classes={classes} style={{paddingTop: '5px'}}>
                    <Select
                        onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                        name={'status'}
                        items={items}
                        id={'status'}
                        value={filter ? filter.value : ''}
                    />
                </MaterialUI.TableCell>);
        } else if (column.name === 'effectivity_date' || column.name === 'termination_date' ) {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
                    <MuiPickersUtilsProvider utils={MomentUtils}>
                        <KeyboardDatePicker
                            variant="dialog"
                            format="MMMM DD, YYYY"
                            inputVariant="outlined"
                            className={datePickerClasses.keyboardDatePicker}
                            value={filter ? filter.value : ''}
                            invalidDateMessage=""
                            onChange={date => {console.log(date); onFilter(date ? { value: date.toISOString() } : null)}}
                        />
                    </MuiPickersUtilsProvider>
                </MaterialUI.TableCell>
            );
        } else if (column.name === 'active_members_count' || column.name === 'for_printing_count' ) {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}></MaterialUI.TableCell>
            )
        } else {
            return (
                <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px'}} classes={classes}>
                    <MaterialUI.OutlinedInput
                        classes={classes}
                        style={{ width: '100%' }}
                        value={filter ? filter.value : ''}
                        labelWidth={0}
                        onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                        placeholder=""
                    />
                </MaterialUI.TableCell>
            );
        }
    } 
  return null;
}

const DefaultClientsToolbar = ({ children }: any) => {
    return <MaterialUI.Toolbar disableGutters={true} children={children} />;
};

let toolbar = DefaultClientsToolbar;

const SortingIcon = ({ direction }: any) =>
    direction === 'asc' ? (
        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
    ) : (
        <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
    );

const ClientsSortLabel = ({ onSort, children, direction }: any) => {
    return (
        <MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort}>
            {children}
            {direction ? (
                <SortingIcon direction={direction} />
            ) : (
                <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
            )}
        </MaterialUI.TableSortLabel>
    );
};

const ClientsSearchPanel = ({ value, onValueChange }: any) => {
    return (
        <MaterialUI.OutlinedInput
            value={value ? value : ''}
            labelWidth={0}
            onChange={e => onValueChange(e.target.value ? e.target.value : null)}
            placeholder="Search clients"
            fullWidth
            startAdornment={
                <MaterialUI.InputAdornment position="start">
                    <FontAwesomeIcon icon={faSearch} />
                </MaterialUI.InputAdornment>
            }
        />
    );
};

interface ProcessBatchesTableProps {
    rows: any,
    columns: any,
    selection: any,
    totalCount: any,
    limit: any,
    sort: any,
    currentPage: any,
    onRowSelect: (selected: any) => void; 
    onSearchChange: (value: any) => void;
    onSortChange: (order: any) => void;
    onPageChange: (limit: any, skip: any) => void;
    onPageSizeChange: (pagesize: any) => void;
    onFilterChange: (filter: any) => void;
}

export const ProcessBatchesTable: React.FC<ProcessBatchesTableProps> = (
    props: ProcessBatchesTableProps
): JSX.Element => {
    const {
      rows,
      columns,
      onRowSelect,
      selection,
      onSearchChange,
      onSortChange,
      onPageChange,
      totalCount,
      limit,
      sort,
      currentPage,
      onPageSizeChange,
      onFilterChange
    } = props;
    const classes = STYLE_CONST.tableStyles();

    

    const getRowId = row => row._id;

    const handleSelection = (newSelection: any) => {
        onRowSelect(newSelection);
    }

    const setCurrentPageMethod = (page) => {
        let skip = (limit * page);

        onPageChange(page, skip);
    }
    
    const changePageSize = (value) => {
        onPageSizeChange(value);
    }


    return (
        <div className={classes.container}>
            <Grid
                rows={rows}
                columns={columns}
                getRowId={getRowId}>

                <PagingState
                    currentPage={currentPage}
                    pageSize={limit}
                    onCurrentPageChange={setCurrentPageMethod}
                    onPageSizeChange={changePageSize}
                />

                <CustomPaging totalCount={totalCount} />

                <SearchState 
                    onValueChange={onSearchChange} />

                <SortingState 
                    sorting={sort}
                    onSortingChange={onSortChange}
                    defaultSorting={[]} 
                />

                <FilteringState 
                    onFiltersChange={onFilterChange}
                    defaultFilters={[]}     
                />

                <SelectionState 
                    selection={selection} 
                    onSelectionChange={handleSelection} />
                <IntegratedSelection />

                <Table columnExtensions={COLUMN_EXTENSIONS} />

                <TableSelection
                    selectByRowClick
                    showSelectionColumn={true}
                />

                <TableHeaderRow
                    sortLabelComponent={ClientsSortLabel}
                    showSortingControls />
                <TableFilterRow cellComponent={ClientsFilterCell} />

                <Toolbar rootComponent={toolbar} />
                <SearchPanel inputComponent={ClientsSearchPanel} />

                <PagingPanel pageSizes={[10, 15, 20]} />
            </Grid>
        </div>
    )
}
