  public static downloadExceptionReport(id: string, data: any) {
    const DOWNLOAD_EXCEPTION_REPORT_URL =
      API_URL + 'member-upload/download/' + id;
    return fetch(DOWNLOAD_EXCEPTION_REPORT_URL, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    })
      .then(response => {
        const responseBlob = response.blob();
        const responseJSON = response.json();
        return responseJSON
          .then(res => {
            if (res.error !== undefined) {
              return responseJSON;
            } else {
              return responseBlob;
            }
          })
          .catch(res => {
            console.log('downloadExceptionReport error: ', res);
            return responseBlob;
          });
      })
      .catch(error => {
        console.log('downloadExceptionReport error: ', error);
      });
  }

