  public static searchMembers(
    limit: number,
    offset: number,
    query: string,
    filters: any,
    sorting: any,
    setQuery?: any,
  ) {
    let CLIENT_URL =
      API_URL +
      `members/search?${setQuery ? setQuery : 'query'}=${query}&limit=${limit}&offset=${offset}&filters=${JSON.stringify(
        filters,
      )}&sorting=${JSON.stringify(sorting)}`;
    if (limit === 0) {
      CLIENT_URL =
        API_URL +
        `members/search?offset=${offset}&filters=${JSON.stringify(
          filters,
        )}&query=${query}`;
    }
    return fetch(CLIENT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
      });
  }

