  public static generateClientReport(
    format: string,
    query: string | null,
    filter: object = {},
  ) {
    const filters = {};

    if (Object.entries(filter).length !== 0) {
      const where = {};
      for (const x in filter) {
        const fil = filter[x];
        if (fil['columnName'] && fil['value']) {
          if (
            ['registered_name', 'corporate_account_no'].includes(
              fil['columnName'],
            )
          ) {
            where[fil['columnName']] = {
              like: fil['value'],
              options: 'i',
            };
          } else if (
            ['prev_hmo_effectivity_date', 'prev_hmo_expiry_date'].includes(
              fil['columnName'],
            )
          ) {
            let date = fil['value'];

            if (moment.isMoment(fil['value'])) {
              date = fil['value'].format('YYYY-MM-DD');
            }

            where[fil['columnName']] = {
              like: date,
              options: 'i',
            };
          } else {
            where[fil['columnName']] = fil['value'];
          }
        }
      }
      filters['where'] = where;
    }
    const REPORT_URL =
      API_URL +
      'clients/generate-report?&format=' +
      format +
      (query ? '&query=' + query : '') +
      (filters['where'] ? '&filter=' + JSON.stringify(filters) : '');
    console.log('Report: ', REPORT_URL, filters);
    return fetch(REPORT_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => {
        const responseBlob = response.blob();
        const responseJSON = response.json();
        return responseJSON
          .then(res => {
            if (res.error !== undefined) {
              return responseJSON;
            } else {
              return responseBlob;
            }
          })
          .catch(res => {
            console.log('downloadExceptionReport error: ', res);
            return responseBlob;
          });
      })
      .catch(error => {
        console.log('client error: ', error);
      });
  }

  static fetchPrinting(
    clientId: string | undefined,
    limit: number,
    offset: number,
    filter?: any,
    order?: any,
    query?: string,
  ) {
    let CLIENTS_URL =
      API_URL +
      `members/list/printing?client_id=${clientId}&limit=${limit}&offset=${offset}`;

    if (query !== null && query !== '') {
      CLIENTS_URL = `${CLIENTS_URL}&query=${query}`;
    }

    if (filter.length !== 0) {
      CLIENTS_URL = `${CLIENTS_URL}&filters=${JSON.stringify(filter)}`;
    }

    if (order.length !== 0) {
      CLIENTS_URL = `${CLIENTS_URL}&order=${JSON.stringify(order)}`;
    }

    return fetch(CLIENTS_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }

  static postPrint(data) {
    let CLIENTS_URL = API_URL + `members/print`;

    return fetch(CLIENTS_URL, {
      method: 'post',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }
