// React imports
import React from 'react'
// Mui imports
import {
    Grid,
    Typography,
    Button
} from '@material-ui/core/';

import {
    Grid as TableGrid,
    Table,
    TableHeaderRow,} from '@devexpress/dx-react-grid-material-ui';
// Relative imports
import { PageHeaderComponent } from 'Components/UI/PageHeader';
import { FloatingButtons } from 'Components/UI/FloatingButtons';
import { Modal } from './Modal';
import { ModalComponent } from 'Components/UI/ModalComponent';
import { Loader } from 'Components/UI';
import { ProcessBatchesTable } from './ProcessBatchesTable';
import MomentUtils from '@date-io/moment';
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import { 
    STYLE_CONST,
    UTIL_CONST,
    COLUMN_PRINCIPAL,
    COLUMN_DEPENDENTS,
    COLUMN_ALL,
    COLUMN_PRINT
} from '../ProcessBatches.const';
// Core imports 
import { Components } from '@hims/core';
// Thirdparty imports
import clsx from 'clsx';
import { API } from '../../API'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faFileAlt
} from '@fortawesome/free-solid-svg-icons';

interface ProcessBatchesProps {
    breadcrumbs: any[]
}

export const ProcessBatches: React.FC<ProcessBatchesProps> = (
    props: ProcessBatchesProps
): JSX.Element => {
    const {
        breadcrumbs
    } = props;
    const classes = STYLE_CONST.batchesWrapper();

    const [activeTab, setActiveTab] = React.useState('principal');
    const [isFetching, setFetching] = React.useState(true);
    const [data, setData] = React.useState<any>({});
    const [members, setMembers] = React.useState<any[]>([]);
    const [selection, setSelection] = React.useState([]);
    const [modalVisibility, setModalVisibility] = React.useState(false);
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0);
    const [limit, setLimit] = React.useState(10);
    const [offset, setOffset] = React.useState(0);
    const [totalCount, setTotalCount] = React.useState();
    const [page, setPage] = React.useState();
    const [query, setQuery] = React.useState('');
    const [order, setOrder] = React.useState<any[]>([]);;
    const [filter, setFilter] = React.useState<any[]>([]);;
    const [sort, setSorting] = React.useState();    
    const [membersPrint, setMembersPrint] = React.useState();    
    const [selectedDate, setSelectedDate] = React.useState<any>(new Date());    
    const [notifModal, setNotifModal] = React.useState<boolean>(false);    
    const [notifModalMessage, setNotifModalMessage] = React.useState<string>('');    
    const [notifModalTitle, setNotifModalTitle] = React.useState<string>('');    
    const [principalCount, setPrincipalCount] = React.useState<number>(0);    
    const [dependentCount, setDependentCount] = React.useState<number>(0);    
    const [totalMembers, setTotalMembers] = React.useState<number>(0);    

    let link = window.location.href;
    let clientId = /[^/]*$/.exec(link)![0];

    const changeTab = (indicator) => {
        setActiveTab(indicator);
    }

    const filterColumn = () => {
        if (activeTab === 'principal') {
            return members.filter(d => {
                return d.member_type === 'Principal'
            })
        }

        if (activeTab === 'dependent') {
            return members.filter(d => {
                return d.member_type === 'Dependent'
            })
        }

        return members
    }

    const onRowSelect = (selected) => {
        setSelection(selected);
    }

    const fetchDefault = () => {
        setFetching(true);

        API.fetchPrinting(clientId, limit, offset, filter, order, query).then((response) => {
            setFetching(false);
            if (!response.error) {
                setData(response);
                setMembers(response.members);
                setTotalMembers(response.members.length);
                if (response.hasOwnProperty('total')) setTotalCount(response.total);
                else setTotalCount(5000);

                let pcount: Array<any> = [];
                let dcount: Array<any> = [];
                response.members.filter((member: any) => {
                    if (member.member_type === 'Principal') pcount.push(member);
                    if (member.member_type === 'Dependent') dcount.push(member);
                });

                setPrincipalCount(pcount.length);
                setDependentCount(dcount.length);

            } else {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
            }
            

            breadcrumbs[1].label = response.registered_name;
        }).catch(err => {
            setNotifModal(true);
            setNotifModalTitle('An error occured while loading the list of members.');
            setNotifModalMessage('Loading failed');
            setFetching(false);
            console.log(err);
        })  
    }

    React.useEffect(() => {
        fetchDefault();
    }, []);

    // const onFilterChange = (filter: any) => {

    // }

    const handleSearch = (value: string ) => {
        console.log('Search: ',value);
        setPage(0);
        setQuery(value);
        if (typingTimeout) {
           clearTimeout(typingTimeout);
        }
        
        setTypingTimeout (setTimeout(function () {
            setFetching(true);
            API.fetchPrinting(clientId, limit, offset, filter, order, value).then((response) => {
                setFetching(false);
                if (!response.error) {
                    setData(response);
                    setMembers(response.members);
                    if (response.hasOwnProperty('total')) setTotalCount(response.total);
                    else setTotalCount(5000);
                } else {
                    setNotifModal(true);
                    setNotifModalTitle('An error occured while loading the list of members.');
                    setNotifModalMessage('Loading failed');
                }
    
            }).catch(err => {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
                setFetching(false);
                console.log(err);
            })  
            }, 3000)
        );
    }

    const handleSortChange = (sort: any) => {
        setSorting(sort);
        setOrder(sort);

        setFetching(true);
        API.fetchPrinting(clientId, limit, offset, filter, sort, query).then((response) => {
            setFetching(false);
            if (!response.error) {
                setData(response);
                setMembers(response.members);
                if (response.hasOwnProperty('total')) setTotalCount(response.total);
                else setTotalCount(5000);
            } else {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
            }

        }).catch(err => {
            setNotifModal(true);
            setNotifModalTitle('An error occured while loading the list of members.');
            setNotifModalMessage('Loading failed');
            setFetching(false);
            console.log(err);
        })
    }

    const handleFilterByColumn = (filter: any) => {
        console.log(filter);
        setPage(0);
          
        setFilter(filter);

        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout (setTimeout(function () {
            setFetching(true);
            API.fetchPrinting(clientId, limit, offset, filter, order, query).then((response) => {
                setFetching(false);
                if (!response.error) {
                    setData(response);
                    setMembers(response.members);
                    if (response.hasOwnProperty('total')) setTotalCount(response.total);
                    else setTotalCount(5000);
                } else {
                    setNotifModal(true);
                    setNotifModalTitle('An error occured while loading the list of members.');
                    setNotifModalMessage('Loading failed');
                }
            }).catch(err => {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
                setFetching(false);
                console.log(err);
            })
            }, 3000)
        );
    }

    const changePageUpdate = (limit: number, skip: number) => {
        setFetching(true);

        setLimit(limit);
        setOffset(skip);

        setFetching(true);
        API.fetchPrinting(clientId, limit, skip, filter, order, query).then((response) => {
            setFetching(false);
            if (!response.error) {
                setData(response);
                setMembers(response.members);
                if (response.hasOwnProperty('total')) setTotalCount(response.total);
                else setTotalCount(5000);
            } else {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
            }

        }).catch(err => {
            setNotifModal(true);
            setNotifModalTitle('An error occured while loading the list of members.');
            setNotifModalMessage('Loading failed');
            setFetching(false);
            console.log(err);
        })
    }

    const handlePageChange = (currentPage: number, skip: number) => {
        setPage(currentPage);

        changePageUpdate(limit, skip);
    }

    const handlePageSizeChange = (value) => {
        setLimit(value);

        changePageUpdate(value, (value * page));
    }

    const groupBy = (collection, property) => {
        let i = 0, val, index,
            values: Array<any> = [], result: Array<any> = [];
        for (; i < collection.length; i++) {
            val = collection[i][property];
            index = values.indexOf(val);
            if (index > -1)
                result[index].push(collection[i]);
            else {
                values.push(val);
                result.push([collection[i]]);
            }
        }
        return result;
    }

    const print = () => {
        let membersToPrint: Array<any> = [];

        selection.forEach(s => {
            members.find(member => {
                if (member._id === s) membersToPrint.push(member);
            })
        });

        membersToPrint.forEach((member: any) => {
            if (!member.hasOwnProperty('batch_name')) member.batch_name = ''
        });

        let groupByBatch = groupBy(membersToPrint, 'batch_name');
        let constructedPrintRow: Array<any> = [];

        groupByBatch.forEach((group) => {
            let c = {
                batch_name: group[0].batch_name,
                count: group.length,
                site: group[0].site,
                preview: <Typography className={classes.preview}>Open</Typography>
            }

            constructedPrintRow.push(c);
        })

        setMembersPrint(constructedPrintRow);
        
        setModalVisibility(true);
    }

    const postPrint = () => {
        setFetching(true);
        let membersToPrint: Array<any> = [];

        selection.forEach(s => {
            members.find(member => {
                if (member._id === s) membersToPrint.push(member);
            })
        });

        let body = {
            printed_by: "APD Encoder",
            date_printed: selectedDate.toISOString(),
            members: membersToPrint
        }

        API.postPrint(body).then((response) => {
            setFetching(false);
            if (!response.error) {
                setNotifModal(true);
                setNotifModalTitle('Success');
                setNotifModalMessage(`Count: ${response.count}`);
            } else {
                setNotifModal(true);
                setNotifModalTitle('An error occured while loading the list of members.');
                setNotifModalMessage('Loading failed');
            }
        }).catch(err => {
            setNotifModal(true);
            setNotifModalTitle('An error occured while loading the list of members.');
            setNotifModalMessage('Loading failed');
            setFetching(false);
            console.log(err);
        })
    }

    const modalClose = () => {
        setModalVisibility(false);
    }

    const handleModalClose = () => {
        setNotifModal(false);
    }

    return (
        <div>
            { isFetching ? <Loader /> : '' }
            <Grid
                container
                className={`${clsx('page-header-title')} ${classes.container}`}>
                <Grid item xs={12}>
                    <Grid item xs={12}>
                        <Components.UI.BreadcrumbsComponent items={breadcrumbs} />
                    </Grid>
                    <Grid item xs={12}>
                        <PageHeaderComponent
                            id={'process_batches'}
                            label="Print ID Cards"
                            steps={[]}
                            activeStep={0}
                        />
                    </Grid>
                </Grid>
            </Grid>
            <Grid
                className={classes.body}
                container>
                <Grid item sm={3}>
                    <Typography className={classes.label}>
                        CORPORATE ACCOUNT NO.
                    </Typography>
                    <Typography className={classes.value}>
                        {data.corporate_account_no}
                    </Typography>
                </Grid>
                <Grid item sm={3}>
                    <Typography className={classes.label}>
                        REGISTERED COMPANY NAME
                    </Typography>
                    <Typography className={classes.value}>
                        {data.registered_name}
                    </Typography>
                </Grid>
                <Grid item sm={3}>
                    <Typography className={classes.label}>
                        TRADE COMPANY NAME
                    </Typography>
                    <Typography className={classes.value}>
                        {data.brand_name}
                    </Typography>
                </Grid>
                <Grid item sm={3}>
                    <Typography className={classes.label}>
                        SORTING AND PRINTING INSTRUCTIONS
                    </Typography>
                    <Typography className={classes.read}>
                        <FontAwesomeIcon icon={faFileAlt} /> Read
                    </Typography>
                </Grid>

                <Grid
                    className={classes.tabs}
                    container>
                        <Grid item sm={12}>
                            <Typography className={classes.subHeading}>
                                IDs to Print (168)
                            </Typography>
                        </Grid>
                        {
                            UTIL_CONST.tabs.map(t =>
                                <Grid item sm={4}>
                                    <Typography
                                        className={`${classes.tab} ${activeTab === t.indicator ? classes.activeTab : '' }`}
                                        onClick={() => changeTab(t.indicator)}>
                                            {t.label} ({t.indicator === 'principal' ?
                                            principalCount : t.indicator === 'dependent' ?
                                            dependentCount : t.indicator === 'all' ?
                                            totalMembers : ''})
                                    </Typography>
                                </Grid>
                            )
                        }
                        <Grid item sm={12}>
                            {
                                <ProcessBatchesTable
                                    columns={
                                        activeTab === 'principal' ?
                                        COLUMN_PRINCIPAL : activeTab === 'dependent' ?
                                        COLUMN_DEPENDENTS : activeTab === 'all' ?
                                        COLUMN_ALL : []}
                                    rows={filterColumn()} 
                                    onSearchChange={handleSearch}
                                    currentPage={page}
                                    onPageSizeChange={handlePageSizeChange}
                                    onPageChange={handlePageChange}
                                    onSortChange={handleSortChange}
                                    sort={sort}
                                    limit={limit}
                                    onFilterChange={handleFilterByColumn}
                                    onRowSelect={onRowSelect}
                                    selection={selection}
                                    totalCount={totalCount} />
                            }
                        </Grid>
                </Grid>
                {
                    selection.length > 0 ? 
                        <FloatingButtons 
                            leftButtons={

                                <Button
                                    className={classes.leftButton}
                                    style={{ marginRight: '10px' }}
                                    variant="contained"
                                    color="secondary"
                                    size="small"
                                    onClick={print}
                                > Print ID cards </Button>
                        }/>
                    : ''
                }
            </Grid>
            <Modal
                visibility={modalVisibility}
                close={modalClose}>
                <Typography className={classes.subHeading}>Confirm Printing of Cards</Typography>
                <Typography className={classes.info}>You are about to print ID cards for <strong>{selection.length} members</strong> from these batch (es):</Typography>
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <Typography className={classes.label}>Date printed:</Typography>
                    <KeyboardDatePicker
                        className={classes.datePicker}
                        variant="dialog"
                        format="MMMM DD, YYYY"
                        inputVariant="outlined"
                        value={selectedDate}
                        invalidDateMessage=""
                        onChange={setSelectedDate}
                    />
                </MuiPickersUtilsProvider>
                <TableGrid
                    rows={membersPrint}
                    columns={COLUMN_PRINT}>

                    <Table />
                    <TableHeaderRow />      
                </TableGrid>
                <Grid container className={classes.printButtons} spacing={5}>
                    <Grid className={classes.modalButtonContainerLeft} item sm={6}>
                        <Button
                            className={classes.modalButtonLeft}
                            style={{ marginRight: '10px' }}
                            variant="outlined"
                            color="secondary"
                            size="small"
                            onClick={modalClose}
                        > Cancel </Button>
                    </Grid>
                    <Grid className={classes.modalButtonContainerRight} item sm={6}>
                        <Button
                            className={classes.modalButtonRight}
                            style={{ marginRight: '10px' }}
                            variant="contained"
                            color="secondary"
                            size="small"
                            onClick={postPrint}
                        > Print </Button>
                    </Grid>
                </Grid>
            </Modal>

            <ModalComponent
                id="success_modal"
                isModalOpen={notifModal}
                message={notifModalMessage}
                title={notifModalTitle}
                onClose={handleModalClose} />
        </div>
    )
}
