  generateExeptionReport = () => {
    const partialTable = Object.assign({}, this.state.partialtable);
    const incompleteTable = Object.assign({}, this.state.incompletetable);
    const conflictTable = Object.assign({}, this.state.conflicttable);
    const validationTable = Object.assign({}, this.state.validationtable);
    const disapprovedTable = Object.assign({}, this.state.disapprovetable);
    const approvedTable = Object.assign({}, this.state.approvedtable);
    const allData = {
      partial: partialTable,
      incomplete: incompleteTable,
      conflict: conflictTable,
      validation: validationTable,
      disapprove: disapprovedTable,
      approved: approvedTable,
    };

    let memberUploadData = Object.assign({}, this.state.member_upload_data);
    console.log(
      'generateExeptionReport member_upload_data',
      this.state.member_upload_data,
    );
    console.log('generateExeptionReport partialtable', this.state.partialtable);
    console.log(
      'generateExeptionReport incompletetable',
      this.state.incompletetable,
    );
    console.log(
      'generateExeptionReport conflicttable',
      this.state.conflicttable,
    );
    console.log(
      'generateExeptionReport validationtable',
      this.state.validationtable,
    );
    console.log(
      'generateExeptionReport disapprovetable',
      this.state.disapprovetable,
    );

    memberUploadData.validated_members.forEach((item: any, index: number) => {
      if (
        item.validation_status !== undefined &&
        item.validation_status.status !== undefined
      ) {
        switch (item.validation_status.status) {
          case 'PARTIAL_MATCH':
            partialTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                /** Set action to 'confirmation' if no action is selected. */
                let action = item2.action;
                if (!action || action === '') {
                  action = 'confirmation';
                }

                memberUploadData.validated_members[
                  index
                ].validation_status.action = action;
              }
            });
            break;
          case 'INCOMPLETE_REQUIREMENTS':
            incompleteTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                const { files, remarks } = item2;
                if (files) {
                  const documents: any[] = [];

                  Object.keys(files).map(key => {
                    const { uploaded_file } = files[key];
                    if (uploaded_file) {
                      documents.push(uploaded_file);
                    }
                  });

                  memberUploadData.validated_members[
                    index
                  ].documents = documents;
                }

                if (remarks) {
                  memberUploadData.validated_members[
                    index
                  ].validation_status.remarks = remarks;
                }

                /** Set action to 'confirmation' if no action is selected. */
                let action = item2.incomplete_action;
                if (!action || action === '') {
                  action = 'for confirmation';
                }

                memberUploadData.validated_members[
                  index
                ].validation_status.action = action;
              }
            });
            break;
          case 'CONFLICT_ON_DATA':
            conflictTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                /** Set action to 'confirmation' if no action is selected. */
                let action = item2.conflict_data_action;
                if (!action || action === '') {
                  action = 'for confirmation';
                }

                const { remarks } = item2;
                if (remarks) {
                  memberUploadData.validated_members[
                    index
                  ].validation_status.remarks = remarks;
                }

                memberUploadData.validated_members[
                  index
                ].validation_status.action = action;
              }
            });
            break;
          case 'FOR_VALIDATION':
          case 'FOR_CONFIRMATION':
            validationTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                /** Set action to 'confirmation' if no action is selected. */
                let action = item2.validation_action;
                if (!action || action === '') {
                  action = 'for confirmation';
                }

                const { remarks } = item2;
                if (remarks) {
                  memberUploadData.validated_members[
                    index
                  ].validation_status.remarks = remarks;
                }

                memberUploadData.validated_members[
                  index
                ].validation_status.action = action;
              }
            });
            break;
          case 'DISAPPROVED':
            disapprovedTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                /** Set action to 'confirmation' if no action is selected. */
                let action = item2.disapprove_action;
                if (!action || action === '') {
                  action = 'for confirmation';
                }
                const { remarks } = item2;
                if (remarks) {
                  memberUploadData.validated_members[
                    index
                  ].validation_status.remarks = remarks;
                }

                memberUploadData.validated_members[
                  index
                ].validation_status.action = action;
              }
            });
            break;
          case 'APPROVED':
            approvedTable.rows.forEach(item2 => {
              const temp = item2.member_details;
              if (
                item.first_name === temp.first_name &&
                item.middle_name === temp.middle_name &&
                item.last_name === temp.last_name &&
                item.date_of_birth === temp.date_of_birth
              ) {
                // memberUploadData.validated_members[index].validation_status.action = item2.action;
              }
            });
            break;
        }
      }
    });

    this.setFieldValue('member_upload_data', memberUploadData);
    this.setFieldValue('allData', allData);
    this.setDefaultActions();
    this.patchUploadMember(memberUploadData);
  };

  patchUploadMember = (data: any) => {
    this.setState({
      loading_state: true,
    });
    API.patchUploadMember(this.props.match.params.member_upload_id, data)
      .then(() => {
        this.setState({
          loading_state: false,
          generateFlag: true,
        });
      })
      .catch(() => {
        this.setState({
          loading_state: false,
          generateFlag: true,
        });
      });
  };

  setDefaultActions = () => {
    const tables = [
      'partialtable',
      'incompletetable',
      'conflicttable',
      'validationtable',
      'disapprovetable',
    ];

    forEach(tables, table => {
      let data = {};
      switch (table) {
        case 'partialtable':
          data = this.state.partialtable;
          forEach(data['rows'], (rowData, row) => {
            let action = rowData['action'];
            if (!action || action === '') {
              action = 'confirmation';

              data['rows'][row]['action'] = action;

              const temp = this.state.partial_items;
              if (temp && !isNil(temp[row])) {
                temp[row]['status'] = action;
                this.setFieldValue('partial_items', temp);
              }
            }
          });
          break;

        case 'incompletetable':
          data = this.state.incompletetable;
          forEach(data['rows'], (rowData, row) => {
            const action = rowData['incomplete_action'];
            if (!action || action === '') {
              data['rows'][row]['incomplete_action'] = 'for confirmation';
            }
          });
          break;

        case 'conflicttable':
          data = this.state.conflicttable;
          forEach(data['rows'], (rowData, row) => {
            const action = rowData['conflict_data_action'];
            if (!action || action === '') {
              data['rows'][row]['conflict_data_action'] = 'for confirmation';
            }
          });
          break;

        case 'validationtable':
          data = this.state.validationtable;
          forEach(data['rows'], (rowData, row) => {
            const action = rowData['validation_action'];
            if (!action || action === '') {
              data['rows'][row]['validation_action'] = 'for confirmation';
            }
          });
          break;

        case 'disapprovetable':
          data = this.state.disapprovetable;
          forEach(data['rows'], (rowData, row) => {
            const action = rowData['disapprove_action'];
            if (!action || action === '') {
              data['rows'][row]['disapprove_action'] = 'for confirmation';
            }
          });
          break;
      }

      this.setFieldValue(table, data);
    });
  };

