  handleSortingChange = (sorting: any) => {
    let data = Object.assign({}, this.state.alltable);
    let all = Object.assign({}, this.state.alltableCopy);
    let sort_name: string = '';
    let sort_issue: string = '';

    sorting.forEach(item => {
      if (item.columnName === 'member_name') sort_name = item.direction;
      if (item.columnName === 'member_issue') sort_issue = item.direction;
    });

    if (sort_name === 'asc') {
      all.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
    }
    if (sort_name === 'desc') {
      all.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
    }
    if (sort_issue === 'asc') {
      all.rows.sort((a, b) => (a.member_issue > b.member_issue ? 1 : -1));
    }
    if (sort_issue === 'desc') {
      all.rows.sort((a, b) => (a.member_issue > b.member_issue ? -1 : 1));
    }

    if (sort_name === 'asc') {
      data.rows.sort((a, b) => (a.member_name > b.member_name ? 1 : -1));
    }
    if (sort_name === 'desc') {
      data.rows.sort((a, b) => (a.member_name > b.member_name ? -1 : 1));
    }
    if (sort_issue === 'asc') {
      data.rows.sort((a, b) => (a.member_issue > b.member_issue ? 1 : -1));
    }
    if (sort_issue === 'desc') {
      data.rows.sort((a, b) => (a.member_issue > b.member_issue ? -1 : 1));
    }

    this.setState({
      alltable: data,
      alltableCopy: all,
    });
  };

  handleSelectedRadio = (
    row: number,
    value: string,
    table: string,
    rowData?: any,
  ) => {
    let data = {};
    switch (table) {
      case 'partialtable':
        data = this.state.partialtable;
        data['rows'][row]['action'] = value;

        const temp = this.state.partial_items;
        if (temp && !isNil(temp[row])) {
          temp[row]['status'] = value;
          this.setFieldValue('partial_items', temp);
        }
        break;
      case 'incompletetable':
        this.setFieldValue('row', row);

        data = this.state.incompletetable;
        if (
          value === 'approve' &&
          data['rows'][row]['incomplete_action'] !== 'approve'
        ) {
          this.openApproveModal();
        } else if (
          value === 'disapprove' &&
          data['rows'][row]['incomplete_action'] !== 'disapprove'
        ) {
          this.openDisapproveModal();
        }

        if (value !== 'approve') {
          delete data['rows'][row]['files'];
        }

        if (value !== 'disapprove') {
          delete data['rows'][row]['remarks'];
        }

        data['rows'][row]['incomplete_action'] = value;
        break;
      case 'conflicttable':
        data = this.state.conflicttable;
        if (value === 'approve' || value === 'disapprove') {
          this.setFieldValue('actionRemarksModalProps', {
            row: row,
            tab: 'Conflict',
            action: value === 'approve' ? 'Approve' : 'Disapprove',
            actionValue: value,
          });
          this.setFieldValue('isActionRemarksModalOpen', true);
        } else {
          data['rows'][row]['conflict_data_action'] = value;
        }
        break;
      case 'validationtable':
        data = this.state.validationtable;
        if (
          rowData &&
          rowData['remarks'] &&
          (rowData['remarks'].toLowerCase() === 'no principal member found' ||
            rowData['remarks'].toLowerCase() ===
              'multiple principal members found') &&
          (value === 'approve' || value === 'disapprove')
        ) {
          this.setState({
            approveDependent: {
              isModalOpen: true,
              possiblePrincipals:
                rowData['member_details']['possible_principals'],
              action: value === 'approve' ? 'Approve' : 'Disapprove',
              row: row,
            },
          });
        } else {
          if (value === 'approve' || value === 'disapprove') {
            this.setFieldValue('actionRemarksModalProps', {
              row: row,
              tab: 'Validation',
              action: value === 'approve' ? 'Approve' : 'Disapprove',
              actionValue: value,
            });
            this.setFieldValue('isActionRemarksModalOpen', true);
          } else {
            data['rows'][row]['validation_action'] = value;
            if (value !== 'approve') {
              data['rows'][row]['member_principal'] = '';
              const targetMembers = this.state.member_upload_data[
                'validated_members'
              ];
              const targetMemberIndex = targetMembers.findIndex(member => {
                return (
                  member['temp_id'] ===
                  data['rows'][row]['member_details']['temp_id']
                );
              });
              targetMembers[targetMemberIndex]['principal_temp_id'] = undefined;
              this.setState({
                member_upload_data: {
                  ...this.state.member_upload_data,
                  validated_members: targetMembers,
                },
              });
              this.setFieldValue('validationtable', data);
            }
          }
        }
        break;
      case 'disapprovetable':
        data = this.state.disapprovetable;

        if (value === 'approve') {
          this.setFieldValue('actionRemarksModalProps', {
            row: row,
            tab: 'Disapproved',
            action: 'Approve',
            actionValue: value,
          });
          this.setFieldValue('isActionRemarksModalOpen', true);
        } else {
          data['rows'][row]['disapprove_action'] = value;
        }

        break;
    }

    this.setFieldValue(table, data);
  };

  handleBack = () => {
    this.setFieldValue('generateFlag', false);
  };

  handleSaveTmp = () => {
    const payload = {
      user_id: 'verify-member',
      ticket_no: this.props.match.params.ticket_id,
      module: 'Membership',
      process: 'Verify Member',
      timestamp: new Date(),
      data: {
        partial_table: this.state.partialtable,
        incomplete_table: this.state.incompletetable,
        conflict_table: this.state.conflicttable,
        validation_table: this.state.validationtable,
        disapprove_table: this.state.disapprovetable,
        approved_table: this.state.approvedtable,
        all_table: this.state.alltable,
      },
    };

    this.setState({
      loading_state: true,
    });

    API.verifyMemberSaveToTmp(payload)
      .then(response => {
        if (response && response.error === null) {
          this.setState({
            loading_state: false,
            snackbarNotifProps: {
              isOpen: true,
              title: '',
              message: 'Details are saved successfully.',
              variant: 'success',
            },
          });
        } else {
          this.setState({
            loading_state: false,
            snackbarNotifProps: {
              isOpen: true,
              title: '',
              message: 'Error saving information',
              variant: 'error',
            },
          });
        }
      })
      .catch(() => {
        this.setState({
          loading_state: false,
          snackbarNotifProps: {
            isOpen: true,
            title: '',
            message: 'Error saving information',
            variant: 'error',
          },
        });
      });
  };

  handleLoadTmp = () => {
    API.loadVerifyDataFromTmp(this.props.match.params.ticket_id).then(res => {
      if (res[0]) {
        let tmpData = res[0];
        this.setState({ tmpDataModal: true, tmpData: tmpData.data });

        localStorage.setItem('tmp_id', tmpData._id);
      }
    });
  };

  setTmpValue = () => {
    let tmpData = this.state.tmpData;

    this.setState({
      partialtable: tmpData.partial_table,
      incompletetable: tmpData.incomplete_table,
      conflicttable: tmpData.conflict_table,
      validationtable: tmpData.validation_table,
      disapprovetable: tmpData.disapprove_table,
      approvedtable: tmpData.approved_table,
      alltable: tmpData.all_table,
      tmpDataModal: false,
    });
  };

  handleTmpModalClose = () => {
    this.setState({
      tmpDataModal: false,
    });
  };

  closeSnackbarNotif = () => {
    this.setState({
      snackbarNotifProps: {
        isOpen: false,
        title: '',
        message: '',
        variant: 'success',
      },
    });
  };

